@use '../inc/vars' as *;

.history {
  background-color: $green;
  .container {

  }
  .section {
    &__header {
  
    }
    &__title {

    }
    &__header-additional {

    }
    &__subtitle {

    }
    &__body {
      
    }
  }
  &__carousel {
    overflow: visible;
    .swiper-slide {
      max-width: 375px;
      @include vertical-borders(0, -60px, 0);
      @media(max-width: $xl){
        
      }
      @media(max-width: $lg){
        
        @include vertical-borders(0, -25px, 0);
      }
      @media(max-width: $md){
        @include vertical-borders(0, -20px, 0);
        max-width: 290px;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
    }
    .swiper__nav {
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        display: none;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
    }
    .swiper-pagination {
      margin-bottom: 0;
    }
  }
  &--no-images {
    padding-top: 0;
    align-items: initial;
    .container {
      display: flex;
      flex-direction: column;

    }
    .section {
      &__header {
        justify-content: flex-end;
        @media(min-width: $lg){
          margin-bottom: 160px;
        }
      }
      &__header-additional {
        @extend .h3 !optional;
        max-width: 1000px;
        min-height: 270px;
        @media(max-width: $xl){
          max-width: 800px;
        }
        @media(max-width: $lg){
          max-width: 600px;
        }
        @media(max-width: $md){
          min-height: auto;
          padding-top: 70px;
        }
        @media(max-width: $sm){
        
        }
        @media(max-width: $xs){
        
        }
        
      }
      &__body {
        margin-top: auto;
        margin-bottom: auto;
      }
    }
    .history-card {
      &__date {
        @extend .h1 !optional;
      }
    }
    .history {
      &__carousel {
        
      }
    }




    .decor {
    background-image: url(../img/svg/decor-history2.svg);
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      max-width: 520px;
      display: none;
    }
    @media(max-width: $sm){
      
    }
    @media(max-width: $xs){
      background-position: bottom right;
      right: 0;
      left: auto;
      &.aos-animate {
        width: 520px;

      }
    
    }
    
  }
  }
}

.history-card {
  display: flex;
  flex-direction: column;
  color: $text;
  gap: 30px;
  &__date {
    font-size: 50px;
    padding-bottom: 30px;
    border-bottom: 1px solid $text;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      font-size: 40px;
      padding-bottom: 15px;
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__list {
    @extend .h5 !optional;
    list-style-type: disc;
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  &__list-el {
  }
  &__img {
    @extend .product-card__img !optional;
  }
  &__price {
    @extend .h3 !optional;
  }
  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
  } 
  &__report {
    @extend .h5 !optional;
  }
  &__button {
    width: 65px;
    height: 65px;
    border-radius: 15px;
    border: 1px solid $text;
    display: flex;
    align-items: center;
    justify-content: center;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      width: 30px;
      height: 30px;
      border-radius: 5px;
      svg {
        width: 15px;
        height: auto;
      }
    }
    @media(max-width: $xs){
    
    }
    
  }
  &:hover {
    .history-card  {
      &__button {
        background: $text;
        svg {
          * {
            stroke: $green;
          }
        }
      }
    }
  }
}